.form-side {
  flex: 0 0 38%;
  padding: 4% 3% 2% 3%;
}

.form_block {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.form_block__logo {
  background: url(../img/Logo.png);
  width: 46.7px;
  height: 84.66px;
}

.form_block__title {
  /* margin-bottom: 45px; 45px */
  height: 36px;
  color: #fff;
  font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
  font-size: 30px;
  line-height: 36px;
}

input,
label,
button {
  display: block;
}

.form_block__form {
  display: flex;
  flex-direction: column;
}

.form_element {
  margin-bottom: 6%; /* 25px */
  font-size: 17px;
}

.default_input {
  padding: 5px;
  background: transparent;
  width: 100%;
  color: #657d95;
  font-size: 12px;
  outline: none;
  border: 1px #657d95 solid;
  border-radius: 5px;
}

.blue:focus {
  border: 1px #1f8efa solid;
  border-radius: 5px;
  color: #1f8efa;
}

.blue:focus::placeholder {
  color: #1f8efa;
}

.sumbit {
  align-self: center;
  width: 100%;
}

.btn {
  width: 100%;
  padding: 10px;
  background-color: #1f8efa;
  font-size: 12px;
  font-weight: 500;
  color: #fff;
  border: none;
  font-weight: 100;
}

.forgot__password {
  /* margin-top: 73px;  73px */
  font-size: 12px;
  color: #657d95;
  line-height: 14px;
  /* margin-bottom: 200px; 200px */
}

.form_block__footer {
  display: flex;
  width: 70%;
  justify-content: center;
}

.dont__have {
  font-size: 12px;
  color: #70889e;
  margin-right: 4%;
}

.sign__up__link {
  color: #1f8efa;
  font-size: 12px;
  cursor: pointer;
}

.description {
  margin-top: 4%; /* 16px */
  font-size: 12px;
  color: #869aac;
  /*margin-bottom: 88px;  88px */
  align-self: center;
}

.have__an {
  margin-right: 2%;
  font-size: 12px;
  color: #869aac;
}

.log_in {
  color: #1f8efa;
  font-size: 12px;
  cursor: pointer;
}

.form_block__form {
  width: 70%;
}

.red {
  border: 1px #e60f0f solid;
  border-radius: 5px;
  color: #e60f0f;
}

.form-error {
  font-size: 12px;
  color: #e60f0f;
  text-align: center;
}

.form-success {
  font-size: 12px;
  color: #53e60f;
  text-align: center;
}

